const authActions = {
   SETUSER : 'SETUSER',
   SETERROR : 'SETERROR',
   SETFIELD : 'SETFIELD',
   LOGIN : 'LOGIN',
   LOGOUT : 'LOGOUT'
}

const initialState = {
    user : {},
    username : '',
    password : '',
    isLoading : false,
    error : '', 
}

export const authReducer = ( state = initialState, action) => {
    switch(action.type){
       case authActions.SETFIELD :
            return { 
                ...state,
                [action.field] : action.value 
            }; break;

       case authActions.SETUSER :
            return { 
                ...state,
                user : action.user, 
                username: '',
                password: '',
                error : 'looking good',
                isLoading : false
            }; break;

       case authActions.SETERROR :
            return { 
                ...state,
                error : action.error,
                user: {},
                username: '',
                password: '',
                isLoading : false 
            }; break;
        
       case authActions.LOGIN :
            return { 
                ...state,
                error : '',
                username: '',
                password: '',
                isLoading : true 
            }; break;
            
       case authActions.LOGOUT :
            return {...initialState, error : ''}; break;

       default:
            return {...state, error : ''}; 
    }
}

