import { actions, setProducts } from '../actions/allactions';
const { REACT_APP_API_URL } = process.env;



const initialState = {
    products: [],
    cart: [],
    session: [],
    recent: []
}


export const loadProducts = () => async(dispatch, getState) => {
    const state = getState();
    try {
        const data = await fetch(`${REACT_APP_API_URL}/products`,{ mode:'cors' }).then(res => res.json())
        if(!data.success) return dispatch(setProducts(state.shopReducer.products));
        return dispatch(setProducts(data.data));
        
    } catch(e){
        return dispatch(setProducts(state.shopReducer.products));
    }
}



export const shopReducer = (state = initialState, action) => {
    
    switch(actions.type){
       
       case actions.SETPRODUCTS :
            return {...state, products : action.products };

       default :
            return {...initialState}; break;
    }
}

