import { Link } from 'react-router-dom';
import Logo from '../assets/images/misc/logo.png';
import Paid from '../assets/images/paid1.jpeg';
import '../pages/Invoice.css';

const PosReceipt = (props) => { 
    return (
		<div id="print">
		<div id="invoice-POS" style={{ padding:'0px' }}>
			<center id="top">
				<div className="info"> 
				    <h3>{ props.ordertype == 'credit' ? 'SALES INVOICE':'SALES RECEIPT'}</h3>
				</div>
			</center>
			<div >
				<div>
					<br/><br/>
					<p> 
						{Logo && <img src={Logo} style={{margin: '-20px 10px 0px 2px ', float: 'left'}} height="100px"/>}
						<b>
							ADDRESS : Abura Market<br />
							PHONE   :    &nbsp;&nbsp;054-043-8290 <br/>
							MOBILE  :    &nbsp;&nbsp;026-928-5774 <br/>
							{ props.ordertype == 'credit' ? 'INVOICE':'RECEIPT'}:  #{props.oid} <br/>
							{ props.created_at ? `DATE: ${props.created_at.toUpperCase()}`  : '' }<br/>
							{ props.refname ? `CLIENT : ${props.refname.toUpperCase()}`  : '' }

						</b>
					</p><br/><br/>
				</div>
			</div>
			<div id="">
			<div id="table">
				<table>
				<tbody>
				<tr className="tabletitle">
					<td>Item</td>
					<td>Unit-Price</td>
					<td>Qty</td>
					<td>Total</td>
				</tr>
			  { props.cart ?
				props.cart.map(inv => (
					<tr className="service" key={inv}>
						<td className="tableitem"><span className="itemtext">{inv.title}</span></td>
						<td className="tableitem"><span className="itemtext">&#8373; {inv.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span></td>
						<td className="tableitem"><span className="itemtext">{inv.quantity}</span></td>
						<td className="tableitem"><span className="itemtext">&#8373; {(inv.price*inv.quantity).toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span></td>
					</tr>
				 ))
				: 
				 (
					<tr className="service">
						<td className="tableitem" colSpan="4"><br/><center className="itemtext"><h3 class="text-dark">NO ITEMS IN ORDER ...</h3></center><br/></td>
					</tr>   
				 )
			  }
				<tr className="tabletitle shead">
				    <td colSpan="1"/>
					<td className="Rate" align="right" colSpan="3">
						<h2>Total :  &#8373;{ props.cart ? props.cart.reduce((acc,ct) => (ct.price*ct.quantity)+acc, 0).toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : 0 }<br/><center><small><em><b>( VAT Inclusive )</b></em></small></center></h2>
					</td>
				</tr>
				</tbody>
				</table>
			</div>
			<div>
			    <center style={{margin: '5px auto 0px'}}><b>GOODS SOLD ARE NOT RETURNABLE</b></center><hr/>
			    {props.ordertype == 'normal' && <center style={{margin: '0px auto'}}><b>THANKS FOR SHOPPING WITH US !!</b></center>}
			    {props.ordertype == 'normal' && Paid && <img src={Paid} style={{margin: '10px auto', display: 'block', height: '40px'}}/>}
				
			</div>
			</div>
		</div>
		{ props.ordertype == 'credit' ? 
		<center><br/>
			<button class="btn btn-inline btn-dark btn-sm mx-5 center" newSale={props.newSale}><b>NEXT SALE</b></button>
		</center>
		: '' }
		<iframe id="printer" style={{height: '0px', width: '0px', position: 'absolute'}}></iframe>
	</div>

    )
}

export default PosReceipt
